.form-container {
  position: relative;
}

.form {
  opacity: 1;
  -webkit-transition: opacity var(--_animspeed-slow) ease-in-out;
  -moz-transition: opacity var(--_animspeed-slow) ease-in-out;
  transition: opacity var(--_animspeed-slow) ease-in-out;
}
.form.is-hidden {
  opacity: 0;
}

.form__item {
  padding: 0 1.5rem;
  margin-bottom: 3rem;
}
.form__item:last-of-type {
  margin-bottom: 0;
}
@media only screen and (min-width: 1400px) {
  .form__item {
    padding: 0 2.5rem;
  }
}

form {
  position: relative;
  width: 100%;
}
form input,
form textarea {
  position: relative;
  display: block;
  width: 100%;
  border: none;
  outline: none;
  margin: 0;
  padding: 1.4rem 0.4rem;
  background-color: transparent;
  border-bottom: 2px solid var(--stroke-elements);
  font: normal 700 2rem/1.4 var(--_font-default);
  color: var(--t-bright);
}
form input:focus,
form textarea:focus {
  border-bottom: 2px solid var(--accent);
}
form textarea {
  height: 16rem;
  resize: none;
}
form ::-webkit-input-placeholder {
  color: var(--t-placeholder);
}
form :-moz-placeholder {
  color: var(--t-placeholder);
}
form ::-moz-placeholder {
  color: var(--t-placeholder);
}
form :-ms-input-placeholder {
  color: var(--t-placeholder);
}
form input:focus:required:invalid,
form textarea:focus:required:invalid {
  color: var(--t-default);
}
form input:required:valid,
form textarea:required:valid {
  color: var(--t-default);
}

.form__reply {
  position: absolute;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  opacity: 0;
  -webkit-transition: opacity var(--_animspeed-slow) ease-in-out;
  -moz-transition: opacity var(--_animspeed-slow) ease-in-out;
  transition: opacity var(--_animspeed-slow) ease-in-out;
}
.form__reply.centered {
  position: absolute;
  top: auto;
  right: auto;
  bottom: auto;
  left: auto;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  max-width: 300px;
}
.form__reply.is-visible {
  opacity: 1;
}

.reply__icon {
  display: block;
  font-size: -moz-calc(var(--_size) * 2.75);
