  .tauthor__name {
    font-size: 3rem;
  }
}

.tauthor__position {
  font: normal 400 1.5rem/1.4 var(--_font-default);
  color: var(--t-medium);
}
.tauthor__position a {
  font-weight: 700;
  color: var(--t-bright);
}
@media only screen and (min-width: 768px) {
  .tauthor__position {
    margin-top: 0.2rem;
  }
}

.tauthor__rating {
  margin: 1rem 0 0.6rem 0;
}
.tauthor__rating i {
  display: block;
  margin-right: 0.8rem;
  font-size: 1.8rem;
  background: var(--t-accent);
  background: -webkit-linear-gradient(15deg, var(--t-accent) 0%, var(--t-secondary) 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.tauthor__rating i:last-of-type {
  margin-right: 0;
}

/* ------------------------------------------------*/
/* Features Testimonials End */
