/* ------------------------------------------------*/
/* Features Slider Start */
/* ------------------------------------------------*/
.tools-slider,
.testimonials-slider {
  position: relative;
  overflow: hidden;
}

.mxd-swiper-pagination.swiper-pagination-bullets {
  position: relative;
  bottom: auto;
  left: auto;
  line-height: 0;
  margin-top: 2rem;
}
.mxd-swiper-pagination .swiper-pagination-bullet {
  display: inline-block;
  width: 10px;
  height: 10px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  line-height: 0;
  background: var(--stroke-elements);
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.mxd-swiper-pagination .swiper-pagination-bullet-active {
  width: 10px;
  background-color: var(--accent);
  background: var(--accent);
  background: -moz-linear-gradient(315deg, var(--accent) 0%, var(--secondary) 100%);
  background: linear-gradient(135deg, var(--accent) 0%, var(--secondary) 100%);
}

.swiper-testimonials {
  padding-bottom: 7rem;
}
@media only screen and (min-width: 1600px) {
  .swiper-testimonials {
    padding-bottom: 8rem;
  }
}

.mxd-slider-btn-square {
  position: absolute;
  top: auto;
  right: auto;
  bottom: 0;
  left: auto;
  width: 5rem;
  height: 5rem;
  margin: 0;
}
.mxd-slider-btn-square::after {
  display: none;
}
@media only screen and (min-width: 1600px) {
  .mxd-slider-btn-square {
    width: 6rem;
    height: 6rem;
  }
}

.mxd-slider-btn-square-prev {
  left: 0;
}

.mxd-slider-btn-square-next {
  left: 6rem;
}
@media only screen and (min-width: 1600px) {
  .mxd-slider-btn-square-next {
    left: 7rem;
  }
}

