/* ------------------------------------------------*/
/* Features Socials Start */
/* ------------------------------------------------*/
.socials-cards {
  padding: 0 0.5rem;
}
@media only screen and (min-width: 1400px) {
  .socials-cards {
    padding: 0 1.5rem;
  }
}

.socials-cards__item {
  width: 50%;
}
@media only screen and (min-width: 768px) {
  .socials-cards__item {
    width: 33.33333334%;
  }
}
@media only screen and (min-width: 1200px) {
  .socials-cards__item {
    width: 20%;
  }
}

.socials-cards__card {
  position: relative;
  width: 100%;
  padding: 3rem;
  background-color: var(--base);
  border: 1px solid var(--stroke-elements);
  -moz-border-radius: var(--_radius-xl);
  border-radius: var(--_radius-xl);
  text-align: center;
  -webkit-transform: scale3d(1, 1, 1);
  -moz-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  will-change: transform;
  -webkit-transition: border 0.3s ease-in-out, background-color 0.3s ease-in-out,
    -webkit-transform 0.3s var(--_animbezier);
  transition: border 0.3s ease-in-out, background-color 0.3s ease-in-out, -webkit-transform 0.3s var(--_animbezier);
  -moz-transition: transform 0.3s var(--_animbezier), border 0.3s ease-in-out, background-color 0.3s ease-in-out,
    -moz-transform 0.3s var(--_animbezier);
  transition: transform 0.3s var(--_animbezier), border 0.3s ease-in-out, background-color 0.3s ease-in-out;
  transition: transform 0.3s var(--_animbezier), border 0.3s ease-in-out, background-color 0.3s ease-in-out,
    -webkit-transform 0.3s var(--_animbezier), -moz-transform 0.3s var(--_animbezier);
}
.socials-cards__card i {
  display: block;
  font-size: 6rem;
  line-height: 1;
  color: var(--t-placeholder);
  -webkit-transition: color 0.3s ease-in-out;
  -moz-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}
.no-touch .socials-cards__card:hover {
  -webkit-transform: scale3d(1.05, 1.05, 1.05);
  -moz-transform: scale3d(1.05, 1.05, 1.05);
  transform: scale3d(1.05, 1.05, 1.05);
  border: 1px solid var(--stroke-controls);
}
.no-touch .socials-cards__card:hover i {
  color: var(--t-bright);
}
@media only screen and (min-width: 1600px) {
  .socials-cards__card {
    font-size: 7rem;
  }
}

.socials-cards__link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.socials-square__link {
  width: 4.4rem;
  height: 4.4rem;
  color: var(--t-bright);
}
.socials-square__link::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -moz-border-radius: 2rem;
  border-radius: 2rem;
  border: 1px solid var(--stroke-controls);
  background-color: var(--base-tint);
  -webkit-transform: scale3d(1, 1, 1);
  -moz-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  will-change: transform;
  -webkit-transition: -webkit-transform 0.3s var(--_animbezier);
  transition: -webkit-transform 0.3s var(--_animbezier);
  -moz-transition: transform 0.3s var(--_animbezier), -moz-transform 0.3s var(--_animbezier);
  transition: transform 0.3s var(--_animbezier);
  transition: transform 0.3s var(--_animbezier), -webkit-transform 0.3s var(--_animbezier),
    -moz-transform 0.3s var(--_animbezier);
}
.socials-square__link i {
  display: block;
  position: relative;
  line-height: 4.4rem;
  font-size: 1.8rem;
  color: var(--t-bright);
  overflow: hidden;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  transition: 0.3s;
}
@media only screen and (min-width: 1200px) {
  .socials-square__link i {
    color: rgba(0, 0, 0, 0);
    text-shadow: 0 0 var(--t-bright), 0 4.4rem var(--t-bright);
  }
  .no-touch .socials-square__link:hover::before {
    -webkit-transform: scale3d(1.08, 1.08, 1.08);
    -moz-transform: scale3d(1.08, 1.08, 1.08);
    transform: scale3d(1.08, 1.08, 1.08);
  }
  .no-touch .socials-square__link:hover i {
    text-shadow: 0 -4.4rem var(--t-bright), 0 0 var(--t-bright);
  }
}
@media only screen and (min-width: 1400px) {
  .socials-square__link {
    width: 5rem;
    height: 5rem;
  }
  .socials-square__link::before {
    -moz-border-radius: 2.2rem;
    border-radius: 2.2rem;
  }
  .socials-square__link i {
    line-height: 5rem;
    font-size: 1.8rem;
    text-shadow: 0 0 var(--t-bright), 0 5rem var(--t-bright);
  }
  .no-touch .socials-square__link:hover i {
    text-shadow: 0 -5rem var(--t-bright), 0 0 var(--t-bright);
  }
}

